import DateFnsUtils from '@date-io/date-fns';
import { Avatar, Box, Button, Grid, IconButton, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import { add } from 'date-fns';
import itLocale from 'date-fns/locale/it';
import { get } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect, useMemo, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import userManager from '../../../context/user/UserManager';
import DialogUsers from '../../Dialog/Users/DialogUsers';
import InputDatePicker from '../../Input/InputDataPicker';
import InputInfo from '../../Input/InputInfo';
import InputSelect from '../../Input/InputSelect';
import InputText from '../../Input/InputText';

import taskDefinitionStyle from './style/taskDefinitionStyle';

const ROLES = ['responsible', 'accountable', 'consulted', 'informed'];

const TaskDefinition = ({ onSubmit, changeTab, store }) => {
  const { t } = useTranslation();
  const {
    register,
    unregister,
    handleSubmit,
    errors,
    watch,
    control,
    setValue,
    clearErrors,
  } = useForm({
    mode: 'all',
    reValidateMode: 'onChange',
  });
  const css = taskDefinitionStyle();

  const { users, fetchUser } = userManager();

  const optionsPriority = useMemo(() => [
    { id: 'low', value: t('entities.creation.taskDefinition.low') },
    { id: 'medium', value: t('entities.creation.taskDefinition.medium') },
    { id: 'high', value: t('entities.creation.taskDefinition.high') },
  ]);
  const [openDialog, setOpenDialog] = useState(false);
  const [roleUser, setRoleUser] = useState(null);
  const [usersSelected, setUsersSelected] = useState();

  useEffect(() => {
    ROLES.forEach(role => {
      register({ name: role }, { required: role === 'responsible' });
    });
    return () => {
      ROLES.forEach(role => unregister(role));
    };
  }, [register]);

  const handleDialogUsers = role => {
    if (role) {
      setRoleUser(role);
    }
    setOpenDialog(prev => !prev);
  };

  const handleRemoveUser = role => {
    setValue(role, undefined);
    setRoleUser(role);
    const newState = { ...usersSelected };
    delete newState[role];
    setUsersSelected(newState);
  };

  const handleUserSelected = user => {
    setUsersSelected(prevState => {
      return { ...prevState, [roleUser]: { ...user, selected: true } };
    });
    setValue(roleUser, user.fiscalCode.toUpperCase());
    clearErrors(roleUser);
  };

  const getPercentageOptions = () => {
    return Array.from(Array(101).keys()).map(element => {
      return { id: element, value: element };
    });
  };

  useEffect(() => {
    fetchUser();
  }, []);

  useEffect(() => {
    if (store) {
      const newUsers = ROLES.filter(role => store[role]).reduce((acc, role) => {
        setRoleUser(role);
        setValue(role, store[role]);
        acc[role] = { id: store[role], selected: true, ...users[store[role]] };
        return acc;
      }, {});
      setUsersSelected(prevState => ({ ...prevState, ...newUsers }));
    }
  }, [store]);

  useEffect(() => {
    if (watch('startDate')) {
      const endDate = add(watch('startDate'), { days: 15 });
      setValue('endDate', endDate);
    }
  }, [watch('startDate')]);

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils} locale={itLocale}>
      <form onSubmit={handleSubmit(onSubmit)}>
        {openDialog ? (
          <DialogUsers
            open={openDialog}
            handleClose={handleDialogUsers}
            handleUserSelected={handleUserSelected}
            usersSelected={usersSelected}
            role={roleUser}
          />
        ) : null}

        <input type="hidden" name="step" defaultValue={2} ref={register} />
        <input type="hidden" name="target" defaultValue="actions" ref={register} />

        <Box className={css.boxGrid}>
          <Grid container className={css.gridContainer} spacing={1}>
            <Grid item xs={6} classes={{ item: css.item }}>
              <Grid container>
                <Grid item xs={12} className={css.boxContainer}>
                  <InputInfo title={t('entities.creation.taskDefinition.task')}>
                    <InputText
                      name="title"
                      placeholder={t('entities.creation.taskDefinition.taskPlaceholder')}
                      unregister={unregister}
                      register={register({
                        required: {
                          value: true,
                          message: t('validation.required'),
                        },
                        maxLength: {
                          value: 125,
                          message: t('validation.maxLenght', { maxLength: 125 }),
                        },
                      })}
                      watch={watch}
                      setValue={setValue}
                      errors={errors}
                      showCountCaracthers
                      countCaracthers={125}
                      initialValue={get(store, 'title')}
                    />
                  </InputInfo>
                </Grid>
                <Grid item xs={12} className={css.boxContainer}>
                  <Box
                    display="flex"
                    flexDirection="row"
                    flexWrap="nowrap"
                    justifyContent="space-between"
                    alignItems="center"
                    alignContent="flex-start"
                  >
                    <Box paddingRight={1}>
                      <InputInfo title={t('entities.creation.taskDefinition.startDate')}>
                        <Box border={0}>
                          <InputDatePicker
                            control={control}
                            name="startDate"
                            setValue={setValue}
                            format="dd MMMM yyyy"
                            rules={{
                              required: {
                                value: true,
                                message: t('validation.required'),
                              },
                            }}
                            errors={errors}
                            initialValue={get(store, 'startDate')}
                          />
                        </Box>
                      </InputInfo>
                    </Box>
                    <Box>
                      <InputInfo title={t('entities.creation.taskDefinition.endDate')}>
                        <Box border={0}>
                          <InputDatePicker
                            control={control}
                            name="endDate"
                            setValue={setValue}
                            format="dd MMMM yyyy"
                            rules={{
                              required: {
                                value: true,
                                message: t('validation.required'),
                              },
                            }}
                            errors={errors}
                            initialValue={get(store, 'endDate')}
                            minDate={add(watch('startDate'), { days: 1 })}
                          />
                        </Box>
                      </InputInfo>
                    </Box>
                  </Box>
                </Grid>
                <Grid item xs={12} className={css.boxContainer}>
                  <InputInfo title={t('entities.creation.taskDefinition.priority')}>
                    <InputSelect
                      register={register({
                        required: {
                          value: true,
                          message: t('validation.required'),
                        },
                      })}
                      emptyOption={t('entities.creation.taskDefinition.priorityPlaceholder')}
                      name="priority"
                      options={optionsPriority}
                      errors={errors}
                      initialValue={get(store, 'priority')}
                    />
                  </InputInfo>
                </Grid>
                <Grid item xs={12} className={css.boxContainer}>
                  <InputInfo title={t('entities.creation.taskDefinition.description')}>
                    <InputText
                      name="description"
                      placeholder={t('entities.creation.taskDefinition.descriptionPlaceholder')}
                      register={register({
                        required: {
                          value: true,
                          message: t('validation.required'),
                        },
                        maxLength: {
                          value: 500,
                          message: t('validation.maxLength', { maxLength: 500 }),
                        },
                      })}
                      watch={watch}
                      setValue={setValue}
                      errors={errors}
                      multiline
                      rows={6}
                      rowsMax={20}
                      showCountCaracthers
                      countCaracthers={500}
                      initialValue={get(store, 'description')}
                      cssClass={css.inputMultiline}
                    />
                  </InputInfo>
                </Grid>
                <Grid item xs={12} className={css.boxContainer}>
                  <InputInfo title={t('entities.creation.taskDefinition.completionPercentage')}>
                    <InputSelect
                      register={register()}
                      watch={watch}
                      setValue={setValue}
                      name="completionPercentage"
                      emptyOption="0"
                      errors={errors}
                      options={getPercentageOptions()}
                    />
                  </InputInfo>
                </Grid>
              </Grid>
            </Grid>
            <Grid item xs={6} classes={{ item: css.item }}>
              <Grid container>
                {ROLES.map(role => (
                  <Grid key={role} item xs={12} className={css.boxContainer}>
                    <InputInfo title={t(`entities.creation.taskDefinition.${role}`)}>
                      <Box
                        className={`${css.borderBox} ${errors[role] ? 'error' : ''}`}
                        textAlign="left"
                        p={2}
                        display="flex"
                        flexDirection="row"
                        flexWrap="nowrap"
                        justifyContent="flex-start"
                        alignContent="flex-start"
                        alignItems="center"
                      >
                        <Box marginRight={2}>
                          {!watch(role) ? (
                            <IconButton
                              fontSize="large"
                              className={css.boxAdd}
                              onClick={() => handleDialogUsers(role)}
                            >
                              <AddIcon className={css.iconAdd} />
                            </IconButton>
                          ) : (
                            <Avatar
                              className={css.avatar}
                              alt={usersSelected[role]?.name}
                              src={usersSelected[role]?.image || ''}
                            />
                          )}
                        </Box>
                        <Box flexGrow={1}>
                          {!watch(role) ? null : (
                            <Typography variant="body1" color="textPrimary">
                              {`${usersSelected[role]?.firstname} ${usersSelected[role]?.lastname}`}
                            </Typography>
                          )}
                          <Typography variant="body2" color="textSecondary">
                            {!watch(role)
                              ? t('entities.creation.taskDefinition.assignRole')
                              : usersSelected[role]?.role}
                          </Typography>
                        </Box>
                        {watch(role) ? (
                          <Box>
                            <IconButton fontSize="large" onClick={() => handleDialogUsers(role)}>
                              <CreateIcon className={css.iconAdd} />
                            </IconButton>
                            {role !== 'responsible' ? (
                              <IconButton fontSize="large" onClick={() => handleRemoveUser(role)}>
                                <DeleteIcon className={css.iconAdd} />
                              </IconButton>
                            ) : null}
                          </Box>
                        ) : null}
                      </Box>
                      {errors[role] ? (
                        <Box marginTop="3px" marginLeft="14px">
                          <Typography variant="caption" color="error" align="left">
                            {t('validation.required')}
                          </Typography>
                        </Box>
                      ) : null}
                    </InputInfo>
                  </Grid>
                ))}
              </Grid>
            </Grid>
          </Grid>
          <Box p={2} border={0} textAlign="center" className={css.boxButtons}>
            <Button
              size="large"
              type="button"
              onClick={evt => changeTab(evt, 0)}
              startIcon={<ArrowBackIosIcon />}
              variant="outlined"
              className={css.button}
              disableRipple
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.previous')}
              </Typography>
            </Button>
            <Button
              size="large"
              type="submit"
              endIcon={<ArrowForwardIosIcon />}
              variant="outlined"
              className={css.button}
              disableRipple
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.next')}
              </Typography>
            </Button>
          </Box>
        </Box>
      </form>
    </MuiPickersUtilsProvider>
  );
};

TaskDefinition.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  changeTab: PropTypes.func.isRequired,
  store: PropTypes.shape({
    responsible: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    accountable: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    consulted: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    informed: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  }).isRequired,
};

export default TaskDefinition;
