import { Box, IconButton, Typography } from '@material-ui/core';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import DeleteIcon from '@material-ui/icons/Delete';

import LinkIcon from '@material-ui/icons/Link';
import PropTypes from 'prop-types';
import React from 'react';

import attachmentInfoStyle from './style/attachmentInfoStyle';
import AttachmentManager from '../../../context/attachment/AttachmentManager';

const AttachmentInfo = ({ attachment, handleDelete, allClouds }) => {
  const css = attachmentInfoStyle();
  const {
    id,
    // eslint-disable-next-line react/prop-types
    file: { name, cloudType, signature },
  } = attachment;

  const link = name;
  let label = name;
  if (label.length > 60) {
    label = `${name.substring(0, 17)}...${name.substring(name.length - 40)}`;
  }
  const { downloadAdhocAttachment } = AttachmentManager();

  return (
    <Box
      className={`${css.borderBox} ${css.marginBox}`}
      textAlign="left"
      p={2}
      display="flex"
      flexDirection="row"
      flexWrap="nowrap"
      justifyContent="space-between"
      alignItems="center"
      alignContent="flex-start"
    >
      <Box p={1} className={css.icon}>
        <AttachFileIcon />
      </Box>
      <Box className={css.attachment}>
        {cloudType === allClouds.NEXTCLOUD ? (
          <Typography variant="body1" color="textSecondary" title={link}>
            <a target="_blank" rel="noreferrer" href={link} className={css.link}>
              {label}
            </a>
          </Typography>
        ) : (
          <>
            {signature ? (
              <Typography
                component="div"
                variant="subtitle2"
                noWrap
                style={{ cursor: 'pointer' }}
                onClick={() => downloadAdhocAttachment(id, null, true)}
              >
                {signature}
              </Typography>
            ) : null}

            <Typography
              variant="body1"
              color="textSecondary"
              title={label}
              component="div"
              noWrap
              style={{ cursor: 'pointer' }}
              onClick={() => downloadAdhocAttachment(id, null, true)}
            >
              {label}
            </Typography>
          </>
        )}
      </Box>
      <Box p={1} className={css.icon}>
        {cloudType === allClouds.NEXTCLOUD ? (
          <a target="_blank" rel="noreferrer" href={link} className={css.link}>
            <LinkIcon />
          </a>
        ) : (
          <LinkIcon
            style={{ cursor: 'pointer' }}
            onClick={() => downloadAdhocAttachment(id, null, true)}
          />
        )}
      </Box>
      <Box className={css.boxAttachmentInfoAction}>
        <IconButton aria-label="delete" onClick={() => handleDelete(id)} disableRipple>
          <DeleteIcon />
        </IconButton>
      </Box>
    </Box>
  );
};

AttachmentInfo.propTypes = {
  attachment: PropTypes.shape({
    id: PropTypes.string,
    file: PropTypes.shape({
      name: PropTypes.string,
      cloudType: PropTypes.string,
    }),
  }).isRequired,
  handleDelete: PropTypes.func.isRequired,
  allClouds: PropTypes.shape(null).isRequired,
};
export default AttachmentInfo;
