import { Box, Container, Paper, Slide, Tab, Tabs } from '@material-ui/core';

import { omit } from 'lodash';
import { SnackbarProvider } from 'notistack';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { AuthenticatedView } from '../../auth/KeycloakViews';
import AttachmentProvider from '../../context/attachment/AttachmentProvider';
import LoadingManager from '../../context/loading/LoadingManager';
import MessageManager from '../../context/message/MessageManager';
import NoteProvider from '../../context/note/NoteProvider';
import TaskManager from '../../context/task/TaskManager';
import NotificationContainer from '../common/NotificationContainer';
import Spinner from '../Spinner/Spinner';
import { containerStyle, tabsStyle, tabStyle } from './style/taskCreationPageStyle';
import Actions from './Tabs/Actions';
import Attachments from './Tabs/Attachments';
import Note from './Tabs/Note';
import Setup from './Tabs/Setup';
import TaskDefinition from './Tabs/TaskDefinition';

const a11yProps = index => ({
  id: `simple-tab-${index}`,
  'aria-controls': `simple-tabpanel-${index}`,
});

const TaskCreationPage = ({ keycloak, serviceUrl, serviceUrlAttachments, useMock, callback }) => {
  const [tab, setTab] = useState(0);
  const { t } = useTranslation();
  const { task, createTask, updateTask } = TaskManager();
  const { error, message, clearError, clearMessage } = MessageManager();

  const classesContainer = containerStyle();
  const classesTab = tabStyle();
  const classesTabs = tabsStyle();

  const changeTab = (event, value) => {
    setTab(value);
  };

  const onSubmit = async (formData, event) => {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }
    const { target, action } = formData;
    const data = omit(formData, 'step', 'target', 'action');
    updateTask(data);
    if (action === 'submit') {
      switch (target) {
        case 'task': {
          const payload = { ...task, actions: formData.actions };
          payload.accountable = payload.accountable || null;
          payload.consulted = payload.consulted || null;
          payload.informed = payload.informed || null;
          const json = await createTask(payload);
          if (json) {
            setTab(parseInt(formData.step, 10));
          }
          break;
        }
        case 'note': {
          setTab(parseInt(formData.step, 10));
          break;
        }
        default:
          break;
      }
    } else {
      setTab(parseInt(formData.step, 10));
    }
  };

  const { isLoading } = LoadingManager();

  return (
    <AuthenticatedView keycloak={keycloak}>
      <SnackbarProvider
        maxSnack={10}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        TransitionComponent={Slide}
      >
        <NotificationContainer message={message} callback={clearMessage} status="success" />
        <NotificationContainer message={error} callback={clearError} status="error" />
      </SnackbarProvider>

      <Paper elevation={5}>
        {isLoading ? <Spinner /> : null}
        <Container maxWidth="xl" disableGutters className={classesContainer.container}>
          <Tabs
            value={tab}
            onChange={changeTab}
            variant="fullWidth"
            aria-label="tabs creation"
            classes={classesTabs}
          >
            <Tab
              label={t('entities.creation.tabs.0')}
              /* eslint-disable-next-line react/jsx-props-no-spreading */
              {...a11yProps(0)}
              classes={classesTab}
              disabled={tab > 0}
            />
            <Tab
              label={t('entities.creation.tabs.1')}
              /* eslint-disable-next-line react/jsx-props-no-spreading */
              {...a11yProps(1)}
              classes={classesTab}
              disabled={tab <= 0 || tab > 1}
            />
            <Tab
              label={t('entities.creation.tabs.2')}
              /* eslint-disable-next-line react/jsx-props-no-spreading */
              {...a11yProps(2)}
              classes={classesTab}
              disabled={tab <= 1 || tab > 2}
            />
            <Tab
              label={t('entities.creation.tabs.3')}
              /* eslint-disable-next-line react/jsx-props-no-spreading */
              {...a11yProps(2)}
              classes={classesTab}
              disabled={tab <= 2 || tab > 2}
            />
            <Tab
              label={t('entities.creation.tabs.4')}
              /* eslint-disable-next-line react/jsx-props-no-spreading */
              {...a11yProps(2)}
              classes={classesTab}
              disabled={tab <= 3}
            />
          </Tabs>
          <TabPanel value={tab} index={0}>
            <Setup store={task} onSubmit={onSubmit} useMock />
          </TabPanel>
          <TabPanel value={tab} index={1}>
            <TaskDefinition store={task} onSubmit={onSubmit} changeTab={changeTab} />
          </TabPanel>
          <TabPanel value={tab} index={2}>
            <Actions onSubmit={onSubmit} changeTab={changeTab} />
          </TabPanel>
          {/* eslint-disable-next-line react/jsx-no-comment-textnodes */}
          <TabPanel value={tab} index={3}>
            {/* Manage Attachment */}
            <AttachmentProvider
              keycloak={keycloak}
              serviceUrl={serviceUrl}
              serviceUrlAttachments={serviceUrlAttachments}
              useMock={useMock}
            >
              <Attachments changeTab={changeTab} />
            </AttachmentProvider>
          </TabPanel>
          {/* eslint-disable-next-line react/jsx-no-comment-textnodes */}
          <TabPanel value={tab} index={4}>
            {/* Manage Note */}
            <NoteProvider keycloak={keycloak} serviceUrl={serviceUrl} useMock={useMock}>
              <Note changeTab={changeTab} callback={callback} />
            </NoteProvider>
          </TabPanel>
        </Container>
      </Paper>
    </AuthenticatedView>
  );
};

TaskCreationPage.propTypes = {
  serviceUrl: PropTypes.string.isRequired,
  serviceUrlAttachments: PropTypes.string.isRequired,
  keycloak: PropTypes.shape({}),
  useMock: PropTypes.bool,
  callback: PropTypes.func,
};

TaskCreationPage.defaultProps = {
  keycloak: {},
  useMock: false,
  callback: () => {},
};

const TabPanel = props => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      /* eslint-disable-next-line react/jsx-props-no-spreading */
      {...other}
    >
      {value === index && <Box p={0}>{children}</Box>}
    </div>
  );
};

TabPanel.propTypes = {
  children: PropTypes.node.isRequired,
  value: PropTypes.number.isRequired,
  index: PropTypes.number.isRequired,
};

export default TaskCreationPage;
