import { getDefaultOptions, request } from 'api/helpers';

const resource = 'tasks';

export const apiTaskGet = async (serviceUrl, taskId) => {
  const url = `${serviceUrl}/${resource}/${taskId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiTaskPost = async (serviceUrl, task) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: task ? JSON.stringify(task) : null,
  };
  return request(url, options);
};
