import { FormControl, Select, FormHelperText } from '@material-ui/core';
import { isEmpty } from 'lodash';

import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import selectStyle from './style/inputSelectStyle';

const InputSelect = ({ register, name, options, emptyOption, errors, initialValue }) => {
  const [value, setValue] = useState('');
  const css = selectStyle();
  const handleChange = event => {
    setValue(event.target.value);
  };
  useEffect(() => {
    if (initialValue) {
      handleChange({ target: { value: initialValue } });
    }
  }, [initialValue]);

  return (
    <FormControl
      variant="outlined"
      className={css.formControl}
      fullWidth
      error={!isEmpty(errors[name])}
    >
      <Select native value={value} onChange={handleChange} inputRef={register} name={name}>
        <option value="" disabled>
          {emptyOption}
        </option>
        {options.map(m => (
          <option key={m.id} value={m.id}>
            {m.value}
          </option>
        ))}
      </Select>
      <FormHelperText id="component-task-name-error-text">{errors[name]?.message}</FormHelperText>
    </FormControl>
  );
};

InputSelect.propTypes = {
  register: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  emptyOption: PropTypes.string.isRequired,
  options: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  errors: PropTypes.shape({}).isRequired,
  initialValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

InputSelect.defaultProps = {
  initialValue: '',
};

export default InputSelect;
