import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import MessageManager from '../message/MessageManager';
import AttachmentContext from './AttachmentContext';
import { apiAttachmentGetAllReferenceAndCloudType } from '../../api/attachments';

const AttachmentProvider = ({ children, keycloak, serviceUrl, serviceUrlAttachments, useMock }) => {
  const [state, setState] = useState({
    keycloak,
    serviceUrl,
    serviceUrlAttachments,
    attachments: [],
    error: null,
    message: null,
    useMock,
    allClouds: {},
    allReferenceType: {},
  });
  const { t } = useTranslation();
  const { setError } = MessageManager();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const json = await apiAttachmentGetAllReferenceAndCloudType(serviceUrlAttachments);
        setState(prevState => ({
          ...prevState,
          allClouds: json.payload.clouds,
          allReferenceType: json.payload.references,
        }));
      } catch (e) {
        setState({ ...state, error: t('error.dataLoading'), message: null });
        setError(t('error.dataLoading'));
      }
    };
    // eslint-disable-next-line react/prop-types
    if (keycloak.initialized && keycloak.authenticated) {
      setState(prevState => ({ ...prevState, keycloak }));
      fetchData();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [keycloak]);

  return (
    <AttachmentContext.Provider value={[state, setState]}>{children}</AttachmentContext.Provider>
  );
};

AttachmentProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  keycloak: PropTypes.shape({}).isRequired,
  serviceUrl: PropTypes.string.isRequired,
  serviceUrlAttachments: PropTypes.string.isRequired,
  useMock: PropTypes.bool.isRequired,
};

export default AttachmentProvider;
