import { getDefaultOptions, request } from 'api/helpers';

const resource = 'tasks';

export const apiNotesGet = async (serviceUrl, taskId) => {
  const url = `${serviceUrl}/${resource}/${taskId}/notes`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiNotesPost = async (serviceUrl, taskId, note) => {
  const url = `${serviceUrl}/${resource}/${taskId}/notes`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: note ? JSON.stringify(note) : null,
  };
  return request(url, options);
};

export const apiNotesDelete = async (serviceUrl, noteId) => {
  const url = `${serviceUrl}/notes/${noteId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options, false);
};
