import { Box, Button, Typography } from '@material-ui/core';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { get } from 'lodash';
import { mockProcessModel } from '../../__mocks__/mocks';
import InputSelect from '../../Input/InputSelect';
import setupStyle from './style/setupStyle';

// eslint-disable-next-line no-unused-vars
const Setup = ({ store, onSubmit, useMock }) => {
  const [process, setProcess] = useState([]);
  const { t } = useTranslation();
  const { register, handleSubmit, errors, setValue, formState } = useForm({
    mode: 'all',
  });
  const css = setupStyle();
  const { processDefinitionId } = store;

  useEffect(() => {
    if (processDefinitionId) {
      setValue('processDefinitionId', processDefinitionId);
    }
  }, [setValue, processDefinitionId]);

  useEffect(() => {
    if (useMock) {
      setProcess(mockProcessModel);
    }
  }, [useMock]);

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <input type="hidden" name="step" defaultValue={1} ref={register} />
      <input type="hidden" name="target" defaultValue="task" ref={register} />
      <Box className={css.boxGrid}>
        <Box border={0} width={1} my={20}>
          <Box border={0} py={2} width="50%" mx="auto">
            <Box p={4} border={0}>
              <Typography variant="subtitle2" color="textPrimary">
                {t('entities.creation.setup.title')}
              </Typography>
            </Box>
            <Box p={2}>
              <Box px={2} border={0}>
                <Typography variant="body2" color="textSecondary">
                  {t('entities.creation.setup.processModel')}
                </Typography>
              </Box>
              <Box>
                <InputSelect
                  register={register({
                    required: {
                      value: true,
                      message: t('validation.required'),
                    },
                  })}
                  name="processDefinitionId"
                  initialValue={get(store, 'processDefinitionId', '')}
                  emptyOption={t('entities.creation.setup.processModelPlaceholder')}
                  options={process}
                  errors={errors}
                />
              </Box>
            </Box>
          </Box>
        </Box>
        <Box borderTop={1} width={1} borderColor="rgba(0, 0, 0, 0.12)">
          <Box border={0} py={2} paddingRight="32px" width="50%" mx="auto" textAlign="right">
            <Button
              size="large"
              type="submit"
              endIcon={<ArrowForwardIosIcon />}
              variant="outlined"
              className={css.button}
              disabled={formState.isSubmitting}
              disableRipple
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.next')}
              </Typography>
            </Button>
          </Box>
        </Box>
      </Box>
    </form>
  );
};

Setup.propTypes = {
  store: PropTypes.shape({
    processDefinitionId: PropTypes.string,
  }).isRequired,
  onSubmit: PropTypes.func.isRequired,
  useMock: PropTypes.bool,
};

Setup.defaultProps = {
  useMock: false,
};

export default Setup;
