import DateFnsUtils from '@date-io/date-fns';
import { Box, Button, Container, Grid, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import MessageIcon from '@material-ui/icons/Message';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import NoteManager from '../../../context/note/NoteManager';
import TaskManager from '../../../context/task/TaskManager';
import UserManager from '../../../context/user/UserManager';
import DialogNote from '../../Dialog/Note/DialogNote';
import NoteInfo from './NoteInfo';
import noteStyle from './style/noteStyle';

const Note = ({ changeTab, callback }) => {
  const { t } = useTranslation();
  const [openDialog, setOpenDialog] = useState(false);
  const css = noteStyle();
  const { logged, users } = UserManager();
  const { task } = TaskManager();
  const { postNote, deleteNote, notes } = NoteManager();

  const handleDialogNote = () => {
    setOpenDialog(prev => !prev);
  };

  const addNote = note => {
    const payload = {
      id: notes.length + 1,
      createdDate: '2020-12-16T09:49:05.331Z',
      createdBy: `${users[logged]?.firstname} ${users[logged]?.lastname}`,
      description: note,
    };

    postNote(payload);
  };

  const removeNote = id => {
    deleteNote(id);
  };

  const handleClick = () => {
    if (callback) {
      callback(task.id);
    }
  };

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <DialogNote
        open={openDialog}
        handleClose={handleDialogNote}
        handleAddNote={note => addNote(note)}
      />

      <Box className={css.boxGrid}>
        <Grid container className={css.gridContainer} spacing={1}>
          <Grid item xs={6} classes={{ item: css.item }}>
            <Container fixed disableGutters className={css.containerInfo}>
              <Container fixed disableGutters className={css.containerInfoScroll}>
                <Box className={css.boxNote}>
                  {notes
                    .sort((a, b) => (a.id > b.id ? -1 : 1))
                    .map((n, index) => (
                      <NoteInfo
                        key={n.id}
                        note={n}
                        index={index + 1}
                        size={notes.length}
                        handleDelete={removeNote}
                      />
                    ))}
                </Box>
              </Container>
            </Container>
          </Grid>
          <Grid item xs={6} classes={{ item: css.item }}>
            <Box className={css.boxEmptyActionContainer}>
              <Box className="boxIcon">
                <MessageIcon />
              </Box>
              <Box>
                <Typography variant="body1" color="textPrimary">
                  {t('entities.creation.note.addNote')}
                </Typography>
              </Box>
              <Box>
                <Typography variant="body1" color="textSecondary">
                  <Trans t={t} i18nKey="entities.creation.note.addNoteDescription">
                    {[<span key={0} className={css.highlight} />]}
                  </Trans>
                </Typography>
              </Box>
              <Box className={css.boxButtonNote}>
                <Button
                  size="large"
                  startIcon={<AddIcon />}
                  variant="contained"
                  className={css.buttonNote}
                  onClick={handleDialogNote}
                  disableRipple
                >
                  <Typography variant="body2" color="textSecondary">
                    {t('entities.creation.note.addButton')}
                  </Typography>
                </Button>
              </Box>
            </Box>
          </Grid>
        </Grid>
        <Box p={2} border={0} textAlign="center" className={css.boxButtons}>
          <Button
            size="large"
            type="button"
            onClick={evt => changeTab(evt, 3)}
            startIcon={<ArrowBackIosIcon />}
            variant="outlined"
            disabled
            className={css.button}
            disableRipple
          >
            <Typography variant="body2" color="textSecondary">
              {t('entities.creation.buttons.previous')}
            </Typography>
          </Button>
          <Button
            size="large"
            onClick={handleClick}
            variant="outlined"
            className={`${css.button} ${css.buttonPublish}`}
            disableRipple
          >
            <Typography variant="body2">{t('entities.creation.buttons.publish')}</Typography>
          </Button>
        </Box>
      </Box>
    </MuiPickersUtilsProvider>
  );
};

Note.propTypes = {
  changeTab: PropTypes.func.isRequired,
  callback: PropTypes.func,
};

Note.defaultProps = {
  callback: () => {},
};

export default Note;
