import { Box, FormControl, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import Switch from 'react-switch';
import switchStyle from './style/inputSwitchStyle';

const InputSwitch = ({ label, on, off, onChange }) => {
  const css = switchStyle();
  const [checked, setChecked] = React.useState(true);
  const handleChange = value => {
    setChecked(value);
    if (onChange) {
      onChange(value);
    }
  };

  return (
    <FormControl variant="outlined" fullWidth>
      <Box position="relative" width={1}>
        <Box className={css.boxSwitch}>
          <Box>
            <Typography variant="body2" color="textSecondary">
              {label}
            </Typography>
          </Box>
          <Switch
            onChange={handleChange}
            checked={checked}
            className={css.switch}
            width={70}
            onColor="#2d2b1f"
            checkedIcon={<div className={css.checkedIcon}>{on}</div>}
            uncheckedIcon={<div className={css.uncheckedIcon}>{off}</div>}
          />
        </Box>
      </Box>
    </FormControl>
  );
};

InputSwitch.propTypes = {
  label: PropTypes.string.isRequired,
  on: PropTypes.string.isRequired,
  off: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
};
export default InputSwitch;
