import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiTaskGet, apiTaskPost } from '../../api/task';
import MessageManager from '../message/MessageManager';
import TaskContext from './TaskContext';
import loadingManager from '../loading/LoadingManager';

const TaskManager = () => {
  const [state, setState] = useContext(TaskContext);
  const { setLoading } = loadingManager();
  const { t } = useTranslation();
  const { serviceUrl, useMock } = state;
  const { setError, setMessage } = MessageManager();

  const createTask = async payload => {
    try {
      setLoading(true);
      if (useMock) {
        await new Promise(resolve => setTimeout(() => resolve(payload)), 500);
        setState(prev => ({
          ...prev,
          task: { id: '33' },
          message: t('message.taskCreated', { id: 33 }),
        }));
        setMessage(t('message.taskCreated', { id: 33 }));
        return 33;
      }

      const response = await apiTaskPost(serviceUrl, payload);

      if (response.payload.id) {
        const task = await apiTaskGet(serviceUrl, response.payload.id);

        const updatedTask = { ...task.payload, processDefinitionId: payload.processDefinitionId };

        setState(prev => ({
          ...prev,
          task: updatedTask,
          message: t('message.taskCreated', { id: response.payload.id }),
        }));
        setMessage(t('message.taskCreated', { id: response.payload.id }));
      }
      return response.payload.id;
    } catch (error) {
      console.error('error', error);
      setState({ ...state, error: t('error.dataLoading'), message: null });
      setError(t('error.dataLoading'));
    } finally {
      setLoading(false);
    }
    return null;
  };

  const updateTask = task => setState(prev => ({ ...prev, task: { ...prev.task, ...task } }));

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  return {
    contextActions: state.actions,
    actions: state.actions,
    task: state.task,
    updateTask,
    createTask,
    error: state.error,
    message: state.message,
    clearError,
    clearMessage,
  };
};

export default TaskManager;
