import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import loadingManager from '../loading/LoadingManager';
import MessageManager from '../message/MessageManager';
import TaskManager from '../task/TaskManager';
import AttachmentContext from './AttachmentContext';
import {
  ADHOC_API,
  apiAttachmentDelete,
  apiAttachmentGet,
  apiAttachmentPost,
  apiAttachmentSearchAdhoc,
  apiDownloadAdhocFile,
  NEXTCLOUD_API,
} from '../../api/attachments';
import UserManager from '../user/UserManager';

const AttachmentManager = () => {
  const [state, setState] = useContext(AttachmentContext);
  const { logged, vertexId, vertexShortName } = UserManager();
  const { setLoading } = loadingManager();
  const { t } = useTranslation();
  const { serviceUrlAttachments } = state;
  const { task } = TaskManager();
  const { setError, setMessage } = MessageManager();

  const postAttachment = async (endpoint, partialBody, referenceType) => {
    const url = `${serviceUrlAttachments}/${endpoint}`;
    const body = { data: partialBody, referenceId: task.id, referenceType };

    try {
      setLoading(true);
      const response = await apiAttachmentPost(url, body);
      if (response.payload.id) {
        const json = await apiAttachmentGet(serviceUrlAttachments, task.id, referenceType);
        const { attachments } = json.payload;
        const message =
          t('message.attachmentAdded') +
          (state.allReferenceType.TASK === referenceType ? ' al task' : '');
        setState(prev => ({
          ...prev,
          attachments,
          message,
        }));
        setMessage(message);
      }
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
      setError(t('error.dataLoading'));
    } finally {
      setLoading(false);
    }
  };

  const postNextcloudAttachment = async (nextcloudLink, referenceType) => {
    postAttachment(NEXTCLOUD_API, nextcloudLink, referenceType);
  };

  const postAdhocAttachment = async (itemsToBeAdded, referenceType) => {
    postAttachment(ADHOC_API, { items: itemsToBeAdded }, referenceType);
  };

  const deleteAttachment = async (attachmentId, referenceType) => {
    try {
      setLoading(true);
      const response = await apiAttachmentDelete(serviceUrlAttachments, attachmentId);
      if (response.payload.id) {
        const json = await apiAttachmentGet(serviceUrlAttachments, task.id, referenceType);
        const { attachments } = json.payload;
        const message =
          t('message.attachmentDeleted') +
          (state.allReferenceType.TASK === referenceType ? ' dal task' : '');
        setState(prev => ({
          ...prev,
          attachments,
          message,
        }));
        setMessage(message);
      }
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
      setError(t('error.dataLoading'));
    } finally {
      setLoading(false);
    }
  };

  const setErrorMessage = (e, suffix) => {
    let errorType = 'dataLoading';
    if (
      e
        .toString()
        .toLowerCase()
        .indexOf('time-out') !== -1
    ) {
      errorType = 'adhocTimeout';
    } else if (
      e
        .toString()
        .toLowerCase()
        .indexOf('bad request') !== -1
    ) {
      errorType = 'adhocLogin';
    } else if (
      e
        .toString()
        .toLowerCase()
        .indexOf('precondition failed') !== -1
    ) {
      errorType = 'adhocConnectionNotFound';
    } else if (
      e
        .toString()
        .toLowerCase()
        .indexOf('not found') !== -1
    ) {
      errorType = `adhoc${suffix}`;
    }
    const errorMessage = t(`error.${errorType}`)
      .replace('{vertexId}', vertexId)
      .replace('{vertexShortName}', vertexShortName);
    setState({ ...state, error: errorMessage, message: null });
    setError(errorMessage);
  };

  const searchAdhocAttachment = async adhocSearchDto => {
    try {
      setLoading(true);
      const params = { ...adhocSearchDto, cf: logged, vertexId };
      const json = await apiAttachmentSearchAdhoc(serviceUrlAttachments, params);
      return json.payload.items;
    } catch (e) {
      setErrorMessage(e, 'Search');
      return [];
    } finally {
      setLoading(false);
    }
  };

  const downloadAdhocAttachment = async (id, name, previewPdf) => {
    try {
      setLoading(true);
      const downloadDto = { id, name, cf: logged, vertexId };
      await apiDownloadAdhocFile(serviceUrlAttachments, downloadDto, previewPdf);
    } catch (e) {
      setErrorMessage(e, 'Download');
    } finally {
      setLoading(false);
    }
  };

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  return {
    attachments: state.attachments,
    postNextcloudAttachment,
    postAdhocAttachment,
    searchAdhocAttachment,
    deleteAttachment,
    downloadAdhocAttachment,
    error: state.error,
    message: state.message,
    clearError,
    clearMessage,
    allClouds: state.allClouds,
    allReferenceType: state.allReferenceType,
  };
};
export default AttachmentManager;
