import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { apiUserFromFiscalCodeGet } from '../../api/users';
import { userMock } from '../../components/__mocks__/mocks';
import UserContext from './UserContext';

const UserProvider = ({ children, keycloak, serviceUrl, useMock }) => {
  const [state, setState] = useState({
    logged: null,
    users: null,
    vertexId: null,
    vertexShortName: null,
    pagination: {
      totalPages: 0,
      totalElements: 0,
      numberOfElements: 0,
      number: 0,
      size: 0,
      first: true,
      last: true,
    },
    useMock,
    serviceUrl,
    error: null,
    message: null,
  });

  const extractVertexId = edo => {
    if (edo) {
      if (edo.type && edo.type.id === 1) {
        return { vertexId: edo.id, vertexShortName: edo.shortName };
      }
      return extractVertexId(edo.parentEdo);
    }
    return null;
  };

  useEffect(() => {
    const { initialized, authenticated } = keycloak;
    const fetchLoggedUser = async () => {
      try {
        if (useMock) {
          const user = userMock.payload[0];
          const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
          const { vertexId, vertexShortName } = extractVertexId(user.edo);
          setState(prev => ({
            ...prev,
            logged: 'malessandrini',
            vertexId,
            vertexShortName,
            isAdmin: true,
            userIdLogged: user.id,
            users: {
              [user.fiscalCode.toUpperCase()]: { ...user, role },
            },
          }));
        } else {
          // eslint-disable-next-line react/prop-types
          Promise.resolve(keycloak.loadUserInfo()).then(info => {
            // eslint-disable-next-line camelcase
            const { preferred_username } = info;
            const loadEdoTree = true;
            apiUserFromFiscalCodeGet(serviceUrl, preferred_username, loadEdoTree).then(json => {
              const user = json.payload;
              const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
              const { vertexId, vertexShortName } = extractVertexId(user.edo);
              setState(prev => ({
                ...prev,
                logged: preferred_username.toUpperCase(),
                vertexId,
                vertexShortName,
                users: {
                  [user.fiscalCode.toUpperCase()]: { ...user, role, hidden: true },
                },
              }));
            });
          });
        }
      } catch (e) {
        console.error('error', e);
      }
    };
    if (initialized && authenticated) {
      fetchLoggedUser();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [keycloak, useMock]);

  return <UserContext.Provider value={[state, setState]}>{children}</UserContext.Provider>;
};

UserProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  keycloak: PropTypes.shape({
    initialized: PropTypes.bool,
    authenticated: PropTypes.bool,
  }),
  serviceUrl: PropTypes.string.isRequired,
  useMock: PropTypes.bool,
};

UserProvider.defaultProps = {
  keycloak: {
    initialized: false,
    authenticated: false,
  },
  useMock: false,
};

export default UserProvider;
