import DateFnsUtils from '@date-io/date-fns';
import { Box, Button, Container, Grid, Slide, Typography } from '@material-ui/core';

import AddIcon from '@material-ui/icons/Add';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import { SnackbarProvider } from 'notistack';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Trans, useTranslation } from 'react-i18next';
import AttachmentManager from '../../../context/attachment/AttachmentManager';
import DialogAttachment from '../../Dialog/Attachment/DialogAttachment';
import DialogDHOC from '../../Dialog/Attachment/DialogDhOC';
import DialogElCloud from '../../Dialog/Attachment/DialogElCloud';
import AttachmentInfo from './AttachmentInfo';
import attachmentsStyle from './style/attachmentsStyle';

const Attachments = ({ changeTab }) => {
  const { t } = useTranslation();
  const [openDialog, setOpenDialog] = useState(false);
  const [openDialogDhOC, setOpenDialogDhOC] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [openDialogElCloud, setOpenDialogElCloud] = useState(false);
  const { attachments, deleteAttachment, allClouds, allReferenceType } = AttachmentManager();
  const einetAttachments = attachments.filter(a => a.file.cloudType === allClouds.NEXTCLOUD);
  const adhocAttachments = attachments.filter(a => a.file.cloudType === allClouds.ADHOC);
  const css = attachmentsStyle();

  const referenceType = allReferenceType.TASK;

  const handleDialog = value => {
    setOpenDialog(!openDialog);
    if (value === 'dhoc') {
      setOpenDialogDhOC(true);
    } else if (value === 'elcloud') {
      setOpenDialogElCloud(true);
    }
  };

  const removeAttachment = id => {
    deleteAttachment(id, referenceType);
  };

  return (
    <>
      <SnackbarProvider
        maxSnack={10}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
        TransitionComponent={Slide}
      />

      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <DialogDHOC
          onClose={() => setOpenDialogDhOC(false)}
          open={openDialogDhOC}
          referenceType={referenceType}
        />
        <DialogElCloud
          onClose={() => setOpenDialogElCloud(false)}
          open={openDialogElCloud}
          referenceType={referenceType}
        />

        <Box className={css.boxGrid}>
          <Grid container className={css.gridContainer} spacing={1}>
            <Grid item xs={6} classes={{ item: css.item }}>
              <Container fixed disableGutters className={css.containerInfo}>
                <Container fixed disableGutters className={css.containerInfoScroll}>
                  <Box p={1}>
                    <Typography variant="body2" color="textSecondary">
                      {t('entities.creation.attachment.einetAttachments', {
                        count: einetAttachments.length,
                      })}
                    </Typography>
                  </Box>
                  <Box className={css.boxAttachment}>
                    {einetAttachments
                      ?.sort((a, b) => (a.id > b.id ? -1 : 1))
                      .map((n, index) => (
                        <AttachmentInfo
                          key={n.id}
                          attachment={n}
                          index={index + 1}
                          size={attachments.length}
                          handleDelete={removeAttachment}
                          allClouds={allClouds}
                        />
                      ))}
                  </Box>
                  <Box p={1}>
                    <Typography variant="body2" color="textSecondary">
                      {t('entities.creation.attachment.adhocAttachments', {
                        count: adhocAttachments.length,
                      })}
                    </Typography>
                  </Box>
                  <Box className={css.boxAttachment}>
                    {adhocAttachments
                      ?.sort((a, b) => (a.id > b.id ? -1 : 1))
                      .map((n, index) => (
                        <AttachmentInfo
                          key={n.id}
                          attachment={n}
                          index={index + 1}
                          size={adhocAttachments.length}
                          handleDelete={removeAttachment}
                          allClouds={allClouds}
                        />
                      ))}
                  </Box>
                </Container>
              </Container>
            </Grid>
            <Grid item xs={6} classes={{ item: css.item }}>
              <DialogAttachment onClose={handleDialog} open={openDialog} />
              <Box className={css.boxEmptyActionContainer}>
                <Box className="boxIcon">
                  <AttachFileIcon />
                </Box>
                <Box>
                  <Typography variant="body1" color="textPrimary">
                    {t('entities.creation.attachment.addAttachment')}
                  </Typography>
                </Box>
                <Box>
                  <Typography variant="body1" color="textSecondary">
                    <Trans t={t} i18nKey="entities.creation.attachment.addAttachmentDescription">
                      {[<span key={0} className={css.highlight} />]}
                    </Trans>
                  </Typography>
                </Box>
                <Box className={css.boxButtonAttachment}>
                  <Button
                    size="large"
                    startIcon={<AddIcon />}
                    variant="contained"
                    className={css.buttonAttachment}
                    onClick={() => setOpenDialog(true)}
                    disableRipple
                  >
                    <Typography variant="body2" color="textSecondary">
                      {t('entities.creation.attachment.addButton')}
                    </Typography>
                  </Button>
                </Box>
              </Box>
            </Grid>
          </Grid>
          <Box p={2} border={0} textAlign="center" className={css.boxButtons}>
            <Button
              size="large"
              type="button"
              onClick={evt => changeTab(evt, 0)}
              startIcon={<ArrowBackIosIcon />}
              variant="outlined"
              disabled
              disableRipple
              className={css.button}
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.previous')}
              </Typography>
            </Button>
            <Button
              size="large"
              onClick={evt => changeTab(evt, 4)}
              endIcon={<ArrowForwardIosIcon />}
              variant="outlined"
              className={css.button}
              disableRipple
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.next')}
              </Typography>
            </Button>
          </Box>
        </Box>
      </MuiPickersUtilsProvider>
    </>
  );
};

Attachments.propTypes = {
  changeTab: PropTypes.func.isRequired,
};

export default Attachments;
