import { Avatar, Box, Grid, IconButton, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import { add } from 'date-fns';
import PropTypes from 'prop-types';
import React, { useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import TaskManager from '../../../../context/task/TaskManager';
import DialogUsers from '../../../Dialog/Users/DialogUsers';
import InputDatePicker from '../../../Input/InputDataPicker';
import InputInfo from '../../../Input/InputInfo';
import InputInfoWithPopper from '../../../Input/InputInfoWithPopper';
import InputSwitch from '../../../Input/InputSwitch';
import InputText from '../../../Input/InputText';
import { formAddActionsStyle, formUsersGridStyle } from './style/formAddActionsStyle';

const FormAddAction = ({
  action,
  typeAction,
  register,
  watch,
  errors,
  control,
  setValue,
  clearErrors,
}) => {
  const roles = useMemo(() => ['responsible', 'accountable', 'consulted', 'informed'], []);
  const [openDialog, setOpenDialog] = useState(false);
  const [roleUser, setRoleUser] = useState(null);
  const [usersSelected, setUsersSelected] = useState({});
  const { task } = TaskManager();
  const { t } = useTranslation();
  const cssGridUsers = formUsersGridStyle();
  const css = formAddActionsStyle();

  useEffect(() => {
    setUsersSelected({});
    register({ name: 'responsible' }, { required: true });
    register({ name: 'endDate' });
    register({ name: 'accountable' });
    register({ name: 'consulted' });
    register({ name: 'informed' });
    register({ name: 'automaticComplete' });
  }, [register]);

  const handleDialogUsers = role => {
    setOpenDialog(!openDialog);
    setRoleUser(role);
  };

  const handleRemoveUser = role => {
    setValue(role, undefined);
    setRoleUser(role);
    const newState = { ...usersSelected };
    delete newState[role];
    setUsersSelected(newState);
  };

  const handleUserSelected = user => {
    setUsersSelected(prevState => ({ ...prevState, [roleUser]: user }));
    setValue(roleUser, user.fiscalCode.toUpperCase());
    clearErrors(roleUser);
  };

  const handleChangeSwitch = value => {
    setValue('automaticComplete', value);
  };

  return (
    <Grid container>
      <DialogUsers
        open={openDialog}
        handleClose={handleDialogUsers}
        handleUserSelected={handleUserSelected}
        usersSelected={usersSelected}
        role={roleUser}
      />
      <Grid item xs={12}>
        <InputInfo
          title={
            typeAction === 'action'
              ? t('entities.creation.actions.action')
              : t('entities.creation.actions.subAction')
          }
        >
          <InputText
            name="title"
            placeholder={
              typeAction === 'action'
                ? t('entities.creation.actions.actionPlaceholder')
                : t('entities.creation.actions.subActionPlaceholder')
            }
            register={register({
              required: {
                value: true,
                message: t('validation.required'),
              },
              maxLength: {
                value: 125,
                message: t('validation.maxLenght', { maxLength: 125 }),
              },
            })}
            watch={watch}
            setValue={setValue}
            errors={errors}
            showCountCaracthers
            countCaracthers={125}
          />
        </InputInfo>
      </Grid>
      <Grid item xs={12}>
        <InputInfo title={t('entities.creation.actions.description')}>
          <InputText
            name="description"
            placeholder={t('entities.creation.actions.descriptionPlaceholder')}
            register={register({
              required: {
                value: true,
                message: t('validation.required'),
              },
              maxLength: {
                value: 500,
                message: t('validation.maxLenght', { maxLength: 500 }),
              },
            })}
            watch={watch}
            setValue={setValue}
            errors={errors}
            multiline
            rows={5}
            rowsMax={20}
            showCountCaracthers
            countCaracthers={500}
          />
        </InputInfo>
      </Grid>
      <Grid item xs={12}>
        <Box
          display="flex"
          flexDirection="row"
          flexWrap="nowrap"
          justifyContent="space-between"
          alignContent="flex-start"
          height="120px"
        >
          <Box paddingRight={1} height={1} width={0.5}>
            <InputInfo title={t('entities.creation.actions.end')}>
              <Box border={0}>
                <InputDatePicker
                  control={control}
                  name="endDate"
                  setValue={setValue}
                  initialValue={task.startDate ?? new Date()}
                  format="dd MMMM yyyy"
                  rules={{
                    required: {
                      value: true,
                      message: t('validation.required'),
                    },
                  }}
                  errors={errors}
                  minDate={add(task.startDate, { days: 1 })}
                  maxDate={typeAction === 'action' ? task.endDate : action.endDate}
                  invalidDateMessage={t('error.invalidDateMessage')}
                />
              </Box>
            </InputInfo>
          </Box>

          {typeAction === 'action' ? (
            <Box border={0} height={1} width={0.5}>
              <InputInfoWithPopper
                title={t('entities.creation.actions.completion')}
                contentPopper={{
                  title: t('entities.creation.actions.popper.title'),
                  description: t('entities.creation.actions.popper.description'),
                }}
              >
                <Box border={0}>
                  <InputSwitch
                    label={t('entities.creation.actions.automatic')}
                    on={t('entities.creation.actions.yes')}
                    off={t('entities.creation.actions.no')}
                    onChange={handleChangeSwitch}
                  />
                </Box>
              </InputInfoWithPopper>
            </Box>
          ) : null}
        </Box>
      </Grid>
      <Grid item xs={12}>
        {roles.map(role => (
          <Grid key={role} item xs={12} classes={cssGridUsers}>
            <InputInfo title={t(`entities.creation.actions.${role}`)}>
              <Box className={css.boxContainer}>
                <Box
                  className={`${css.borderBox} ${errors[role] ? 'error' : ''}`}
                  textAlign="left"
                  p={2}
                  display="flex"
                  flexDirection="row"
                  flexWrap="nowrap"
                  justifyContent="flex-start"
                  alignContent="flex-start"
                  alignItems="center"
                >
                  <Box marginRight={2}>
                    {!watch(role) ? (
                      <IconButton
                        fontSize="large"
                        className={css.boxAdd}
                        onClick={() => handleDialogUsers(role)}
                        disableRipple
                      >
                        <AddIcon className={css.iconAdd} />
                      </IconButton>
                    ) : (
                      <Avatar
                        className={css.avatar}
                        alt={usersSelected[role]?.firstname}
                        src={usersSelected[role]?.image || ''}
                        onClick={() => handleDialogUsers(role)}
                      />
                    )}
                  </Box>
                  <Box flexGrow={1}>
                    {!watch(role) ? null : (
                      <Typography variant="body1" color="textPrimary">
                        {`${usersSelected[role].firstname} ${usersSelected[role].lastname}`}
                      </Typography>
                    )}
                    <Typography variant="body2" color="textSecondary">
                      {!watch(role)
                        ? t('entities.creation.actions.assignRole')
                        : usersSelected[role].role}
                    </Typography>
                  </Box>
                  {watch(role) ? (
                    <Box>
                      <IconButton
                        fontSize="large"
                        onClick={() => handleDialogUsers(role)}
                        disableRipple
                      >
                        <CreateIcon className={css.iconAdd} />
                      </IconButton>
                      {role !== 'responsible' ? (
                        <IconButton
                          fontSize="large"
                          onClick={() => handleRemoveUser(role)}
                          disableRipple
                        >
                          <DeleteIcon className={css.iconAdd} />
                        </IconButton>
                      ) : null}
                    </Box>
                  ) : null}
                </Box>
              </Box>
              {errors[role] ? (
                <Box marginTop="3px" marginLeft="14px">
                  <Typography variant="caption" color="error" align="left">
                    {t('validation.required')}
                  </Typography>
                </Box>
              ) : null}
            </InputInfo>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
};

FormAddAction.propTypes = {
  action: PropTypes.shape({
    endDate: PropTypes.instanceOf(Date),
  }).isRequired,
  typeAction: PropTypes.oneOf(['action', 'subAction']).isRequired,
  register: PropTypes.func.isRequired,
  watch: PropTypes.func.isRequired,
  errors: PropTypes.shape({}).isRequired,
  control: PropTypes.shape({}).isRequired,
  setValue: PropTypes.func.isRequired,
  clearErrors: PropTypes.func.isRequired,
};

export default FormAddAction;
