import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import NoteContext from './NoteContext';

const NoteProvider = ({ children, taskId, keycloak, serviceUrl, useMock }) => {
  const [state, setState] = useState({
    taskId,
    notes: [],
    error: null,
    message: null,
    keycloak,
    serviceUrl,
    useMock,
  });

  useEffect(() => {
    // eslint-disable-next-line react/prop-types
    if (keycloak.initialized && keycloak.authenticated) {
      setState(prevState => ({ ...prevState, keycloak }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [keycloak]);

  return <NoteContext.Provider value={[state, setState]}>{children} </NoteContext.Provider>;
};

NoteProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  taskId: PropTypes.string,
  keycloak: PropTypes.shape({}).isRequired,
  serviceUrl: PropTypes.string.isRequired,
  useMock: PropTypes.bool.isRequired,
};

NoteProvider.defaultProps = {
  taskId: null,
};
export default NoteProvider;
