import { Avatar, Box, IconButton, Typography } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { differenceInSeconds, format } from 'date-fns';
import { it } from 'date-fns/locale';
import Interweave from 'interweave';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import noteInfoStyle from './style/noteInfoStyle';

const NoteInfo = ({ note, index, size, handleDelete }) => {
  const { t } = useTranslation();
  const css = noteInfoStyle();
  const { id, createdDate, createdBy } = note;

  return (
    <Box className={css.boxNoteInfoContainer}>
      <Box className={css.boxNoteInfoAction}>
        <Typography variant="body1">{t('entities.creation.note.info', { index, size })}</Typography>
        <IconButton aria-label="delete" onClick={() => handleDelete(id)} disableRipple>
          <DeleteIcon />
        </IconButton>
      </Box>
      <Box className={css.boxNoteInfoText}>
        <Typography variant="body2" component="div">
          <Interweave content={note.description} />
        </Typography>
      </Box>
      <Box className={css.boxNoteInfoUser}>
        <Box p={1} marginRight={2}>
          <Avatar alt={createdBy} />
        </Box>
        <Box>
          <Typography variant="caption" color="textSecondary">
            {differenceInSeconds(new Date(), new Date(createdDate)) < 60
              ? t('entities.creation.note.now')
              : format(new Date(createdDate), 'dd MMMM yyyy: kk:mm:ss', { locale: it })}
          </Typography>
          <Typography variant="body2" color="textPrimary">
            {createdBy}
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};

NoteInfo.propTypes = {
  note: PropTypes.shape({
    id: PropTypes.number,
    createdDate: PropTypes.string,
    createdBy: PropTypes.string,
    description: PropTypes.string,
  }).isRequired,
  index: PropTypes.number.isRequired,
  size: PropTypes.number.isRequired,
  handleDelete: PropTypes.func.isRequired,
};
export default NoteInfo;
