import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiUsersGet } from '../../api/users';
import { userMock } from '../../components/__mocks__/mocks';
import MessageManager from '../message/MessageManager';
import UserContext from './UserContext';

const UserManager = () => {
  // eslint-disable-next-line no-unused-vars
  const [state, setState] = useContext(UserContext);
  const { t } = useTranslation();
  const { serviceUrl, useMock, logged } = state;
  const msgManager = MessageManager();

  const setUsers = users => {
    setState(prevState => ({ ...prevState, users }));
  };

  const setPagination = pagination => setState({ ...state, pagination });

  const fetchUser = async (pagination, search, informedOrConsulted) => {
    try {
      let json;
      if (useMock) {
        json = await new Promise(resolve => setTimeout(() => resolve(userMock)), 100);
      } else {
        json = await apiUsersGet(serviceUrl, pagination, search, informedOrConsulted);
      }
      const {
        payload,
        totalPages,
        totalElements,
        numberOfElements,
        number,
        size,
        first,
        last,
      } = json;
      if (payload) {
        const users = payload.reduce((acc, user) => {
          const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
          acc[user.fiscalCode.toUpperCase()] = {
            ...user,
            role,
          };
          return acc;
        }, {});
        const userLogged = state.users[logged];
        const userPresent = Object.keys(users).find(
          key => users[key]?.fiscalCode.toUpperCase() === logged
        );
        setState(prev => ({
          ...prev,
          users: userPresent
            ? users
            : { ...users, [userLogged?.fiscalCode.toUpperCase()]: userLogged },

          pagination: {
            totalPages,
            totalElements,
            numberOfElements,
            number,
            size,
            first,
            last,
          },
        }));
      }
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
      msgManager.setError(t('error.dataLoading'));
    }
  };

  const setError = error => setState(prev => ({ ...prev, error }));

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const setMessage = message => setState(prev => ({ ...prev, message }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  return {
    logged: state.logged,
    vertexId: state.vertexId,
    vertexShortName: state.vertexShortName,
    users: state.users,
    serviceUrl: state.serviceUrl,
    useMock: state.useMock,
    setUsers,
    setPagination,
    fetchUser,
    setError,
    clearError,
    setMessage,
    clearMessage,
  };
};

export default UserManager;
