import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiNotesDelete, apiNotesGet, apiNotesPost } from '../../api/notes';
import loadingManager from '../loading/LoadingManager';
import MessageManager from '../message/MessageManager';
import TaskManager from '../task/TaskManager';
import NoteContext from './NoteContext';

const NoteManager = () => {
  const [state, setState] = useContext(NoteContext);
  const { setLoading } = loadingManager();
  const { t } = useTranslation();
  const { serviceUrl, useMock } = state;
  const { task } = TaskManager();
  const { setError, setMessage } = MessageManager();

  const postNote = async note => {
    try {
      setLoading(true);
      if (useMock) {
        const response = await new Promise(resolve => setTimeout(() => resolve(note)), 1500);
        setState(prevState => ({
          notes: [...prevState.notes, response],
          message: t('message.noteCreated', { id: 99 }),
        }));
        return 99;
      }
      const response = await apiNotesPost(serviceUrl, task.id, note.description);
      if (response.payload.id) {
        const json = await apiNotesGet(serviceUrl, task.id);
        const notes = json.payload;
        notes.forEach(n => console.table(n));
        setState(prev => ({
          ...prev,
          notes,
          message: t('message.noteCreated', { id: response.payload.id }),
        }));
      }
      setMessage(t('message.noteCreated', { id: response.payload.id }));
      return response.payload.id;
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
      setError(t('error.dataLoading'));
    } finally {
      setLoading(false);
    }
    return null;
  };

  const deleteNote = async noteId => {
    try {
      setLoading(true);

      if (useMock) {
        const notes = await new Promise(
          resolve => setTimeout(() => resolve(state.notes.filter(f => f.id !== noteId))),
          1500
        );
        setState(prevState => ({
          ...prevState,
          notes,
          message: t('message.noteDeleted', { id: noteId }),
        }));
        setMessage(t('message.noteDeleted', { id: noteId }));
        return 99;
      }
      await apiNotesDelete(serviceUrl, noteId);
      const json = await apiNotesGet(serviceUrl, task.id);
      const notes = json.payload;
      setState(prev => ({
        ...prev,
        notes,
        message: t('message.noteDeleted', { id: noteId }),
      }));
      setMessage(t('message.noteDeleted', { id: noteId }));
      return null;
    } catch (e) {
      console.log('error', e);
      setState({ ...state, error: t('error.dataLoading'), message: null });
      setError(t('error.dataLoading'));
    } finally {
      setLoading(false);
    }
    return null;
  };

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  return {
    notes: state.notes,
    postNote,
    deleteNote,
    error: state.error,
    message: state.message,
    clearError,
    clearMessage,
  };
};
export default NoteManager;
