import DateFnsUtils from '@date-io/date-fns';
import { Box, Button, Container, Grid, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import { add } from 'date-fns';
import itLocale from 'date-fns/locale/it';
import { cloneDeep, isEmpty, omit, pick } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Trans, useTranslation } from 'react-i18next';
import TaskManager from '../../../context/task/TaskManager';
import ActionInfo from './ActionInfo';
import FormAddAction from './FormAddAction/FormAddAction';
import { actionsStyle } from './style/actionsStyle';

const initAction = {
  title: 'Azione',
  description: '',
  startDate: null,
  automaticComplete: true,
  responsible: null,
  accountable: null,
  consulted: null,
  informed: null,
  subActions: [],
};

const FIELDS = [
  'title',
  'description',
  'endDate',
  'responsible',
  'accountable',
  'consulted',
  'informed',
];

const Actions = ({ onSubmit, changeTab }) => {
  const [disableAddAction, setDisableAddAction] = useState(false);
  const [disableAddSubAction, setDisableAddSubAction] = useState(false);
  const [actions, setActions] = useState([]);
  const [typeAction, setTypeAction] = useState(null);
  const [indexAction, setIndexAction] = useState(0);
  const { task, contextActions } = TaskManager();
  const {
    register,
    handleSubmit,
    errors,
    watch,
    control,
    setValue,
    trigger,
    clearErrors,
    reset,
  } = useForm({
    mode: 'onChange',
    reValidateMode: 'onBlur',
    defaultValues: { initAction },
  });

  const css = actionsStyle();
  const { t } = useTranslation();

  const usePreviousValues = values => {
    const ref = useRef();
    useEffect(() => {
      ref.current = values;
    });
    return ref.current;
  };

  const preWatch = usePreviousValues(watch(FIELDS));
  const preTypeAction = usePreviousValues(typeAction);

  const handleClickAddAction = async () => {
    if (actions.length > contextActions.length || typeAction === 'subAction') {
      await trigger(FIELDS);
    }
    if (isEmpty(errors)) {
      const newActions = cloneDeep(actions);
      setTypeAction('action');
      newActions.push({ ...initAction });
      setActions(newActions);
      if (actions.length) {
        reset(FIELDS);
      }
      setValue('endDate', add(task.startDate, { days: 1 }));
    }
  };

  const handleClickAddSubAction = async (type, index) => {
    await trigger(FIELDS);
    if (isEmpty(errors)) {
      const newActions = cloneDeep(actions);
      setIndexAction(index);
      reset(FIELDS);
      setTypeAction(type);
      const initSubAction = { ...omit({ ...initAction }, 'subActions', 'automaticComplete') };
      initSubAction.title = 'Sottoazione';
      newActions[index].subActions.push({ ...initSubAction, endDate: actions[index].endDate });
      setActions(newActions);
      setDisableAddSubAction(true);
      setValue('endDate', actions[index].endDate);
    }
  };

  useEffect(() => {
    if (contextActions.length) {
      setActions(contextActions);
    }
  }, [contextActions]);

  useEffect(() => {
    if (preWatch && preTypeAction === typeAction) {
      const fields = watch(FIELDS);
      const changedField = Object.keys(fields).filter(key => fields[key] !== preWatch[key]);
      if (changedField.length) {
        const newActions = cloneDeep(actions);
        const index = newActions.length - 1;
        const fieldsChanged = changedField.reduce((acc, key) => {
          acc[key] = fields[key];
          return acc;
        }, {});
        if (!isEmpty(fieldsChanged)) {
          if (typeAction === 'action') {
            newActions[index] = { ...newActions[index], ...fieldsChanged };
          } else if (typeAction === 'subAction') {
            const indexSubAction = newActions[index].subActions.length - 1;
            newActions[index].subActions[indexSubAction] = {
              ...newActions[index].subActions[indexSubAction],
              ...fieldsChanged,
            };
          }
          setActions(newActions);
          setDisableAddAction(false);
          setDisableAddSubAction(false);
        }
      }
    }
  }, [watch, preWatch, preTypeAction, typeAction]);

  const onSubmitInternal = (data, event) => {
    if (isEmpty(errors)) {
      const payload = { ...pick(data, 'step', 'target', 'action'), actions };
      onSubmit(payload, event);
    } else {
      changeTab(null, 3);
    }
  };

  if (!isEmpty(errors)) {
    console.table(errors);
  }
  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils} locale={itLocale}>
      <form onSubmit={handleSubmit(onSubmitInternal)}>
        <input type="hidden" name="step" defaultValue={3} ref={register} />
        <input type="hidden" name="target" defaultValue="task" ref={register} />
        <input type="hidden" name="action" defaultValue="submit" ref={register} />
        <Box className={css.boxGrid}>
          <Grid container className={css.gridContainer} spacing={1}>
            <Grid item xs={6} classes={{ item: css.item }}>
              <Box className={css.borderBox}>
                <Button
                  variant="contained"
                  type="button"
                  className={css.buttonAddAction}
                  startIcon={<AddIcon />}
                  onClick={() => handleClickAddAction('new')}
                  disabled={disableAddAction}
                  disableRipple
                >
                  <Typography variant="body2" color="textSecondary">
                    {t('entities.creation.actions.add')}
                  </Typography>
                </Button>
              </Box>
              {actions.length > 0 ? (
                <Container fixed disableGutters className={css.containerInfo}>
                  <Container fixed disableGutters className={css.containerInfoScroll}>
                    <Box className={`${css.borderBox} ${css.borderBoxInfo}`}>
                      <Box className={css.boxActions}>
                        <ActionInfo
                          actions={actions}
                          handleClickAction={handleClickAddSubAction}
                          disabledButton={disableAddSubAction}
                        />
                      </Box>
                    </Box>
                  </Container>
                </Container>
              ) : null}
            </Grid>
            <Grid item xs={6} classes={{ item: css.item }}>
              {actions.length <= 0 ? (
                <Box className={css.boxEmptyActionContainer}>
                  <Box className="boxIcon">
                    <LibraryBooksIcon />
                  </Box>
                  <Box>
                    <Typography variant="body1" color="textPrimary">
                      {t('entities.creation.actions.addTaskAction')}
                    </Typography>
                  </Box>
                  <Box>
                    <Typography variant="body1" color="textSecondary">
                      <Trans t={t} i18nKey="entities.creation.actions.addTaskActionDescription">
                        {[<span key={0} className={css.highlight} />]}
                      </Trans>
                    </Typography>
                  </Box>
                </Box>
              ) : (
                <Container fixed disableGutters className={css.containerInfo}>
                  <Container fixed disableGutters className={css.containerInfoScroll}>
                    <Box p={1} className={css.border}>
                      <FormAddAction
                        action={actions[indexAction]}
                        typeAction={typeAction ?? 'action'}
                        register={register}
                        control={control}
                        watch={watch}
                        errors={errors}
                        setValue={setValue}
                        clearErrors={clearErrors}
                      />
                    </Box>
                  </Container>
                </Container>
              )}
            </Grid>
          </Grid>
          <Box p={2} border={0} textAlign="center" className={css.boxButtons}>
            <Button
              size="large"
              type="button"
              onClick={evt => changeTab(evt, 1)}
              startIcon={<ArrowBackIosIcon />}
              variant="outlined"
              className={css.button}
              disableRipple
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.previous')}
              </Typography>
            </Button>
            <Button
              size="large"
              type="submit"
              endIcon={<ArrowForwardIosIcon />}
              variant="outlined"
              className={css.button}
              disableRipple
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.creation.buttons.next')}
              </Typography>
            </Button>
          </Box>
        </Box>
      </form>
    </MuiPickersUtilsProvider>
  );
};

Actions.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  changeTab: PropTypes.func.isRequired,
};

export default Actions;
